














import Vue from "vue";
export default Vue.extend({
  name: "me-filter",
  data() {
    return {
    };
  },
  computed: {
    lazy(this: any): any {
      return this.$store.state.range;
    }
  },
  methods: {
    onChangeValue(val: any) {
      console.log(val);
      this.$store.commit('setRange',val);
    }
  },
});
