


















































import Vue from "vue";
export default Vue.extend({
  name: "me-modal",
  data() {
    return {
      name: "",
      place: "",
      style: "",
      link: "",
      today: new Date(),
      date: new Date(),
      maxDay: new Date(),
      description: ""
    };
  },
  mounted() {
    this.maxDay = new Date();
    this.maxDay.setDate(this.today.getDate() + 90);
  },
  computed: {
    loading(): any {
      return this.$store.state.createItemLoading;
    },
    clickedAddButton(): any {
      return this.$store.state.clickedAddButton;
    }
  },
  watch: {
    clickedAddButton(state): any {
      this.name = "",
      this.date = new Date(),
      this.description = "",
      this.link = "",
      this.place ="", 
      this.style= ""
    }
  },
  methods: {
    cancel() {
      this.$store.commit("unClickAddButton");
    },
    create() {
      this.$store.dispatch("createItem", {
        name: this.name,
        style: this.style,
        link: this.link,
        place: this.place,
        date: this.date.getTime(),
        description: this.description
      });
    }
  }
});
