


























import Vue from "vue";
export default Vue.extend({
  name: "me-search",
  data() {
    return { show: false };
  },
  computed: {
    loading(): boolean {
      return this.$store.state.getItemsLoading;
    }
  },
  methods: {
    onClickButton(range: number) {
      this.$store.commit("setRange", range);
      this.$store.dispatch("getItems");
      this.show = false;
    },
    showTooltip() {
      setTimeout(() => {
        this.show = true;
      }, 500);
    },
    hideTooltip() {
      setTimeout(() => {
        this.show = false;
      }, 10);
    }
  },
  watch: {
    show(value) {}
  }
});
